<template>
  <div class="mainhidden">
    <h3>注释</h3>
    <pre>
     单行注释：// （快捷键ctrl+/）
     多行注释：/* */ （快捷键shift+alt+a）</pre
    >
    <h3>javascript的三种引入方式</h3>
    <p>1、行内使用</p>
    <input type="button" value="唐伯虎" @click="tangbohu" />
    <pre>&lt;input type:"button" value="唐伯虎" οnclick="alert('秋香')"></pre>
    <p>2、内部引入</p>
    <pre>
    &lt;script>
        alert("这是js的内部引入");
    &lt;/script></pre
    >
    <p>3、外部引入</p>
    <pre>&lt;script>type="text/javascript" src="demo.js">&lt;/script></pre>
    <h3>输入框</h3>
    <button @click="shuru">点我试试</button>
    <pre>var a = prompt('请输入年龄'); //a就拿到了你输入的值</pre>
    <h3>输出框</h3>
    <button @click="shuchu">点我试试</button>
    <pre>alert(a);</pre>
    <h3>控制台输出</h3>
    <button @click="log">点我试试</button>
    <pre>
console.log('你好啊！'); //f12打开控制台 //或者右键浏览器区域审查元素 //切换到控制台</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    tangbohu() {
      alert("秋香");
    },
    shuru() {
      window.a = prompt("请输入年龄");
    },
    shuchu() {
      if (!window.a) {
        return alert("你还没有输入年龄哟~");
      }
      alert(window.a);
    },
    log() {
      console.log("你好啊！");
    },
    
  },
};
</script>

<style lang="less" scoped></style>
