<template>
  <div>
    <h3>if分支语句</h3>
    <pre>   //判断a成立吗?成立执行成立-不成立执行else
      var a = true;
      if (a) {
        alert("成立");
      } else {
        alert("不成立");
      }
      <button @click="if1">点我试试</button>
      -----------------------------------------------------
      var a = true;
      var b = true;
      if (a) {
        alert("a成立");
      } else if(b){
        alert("b成立");
      }else{
        alert("都不成立");
      }
      <button @click="if2">点我试试</button>
      </pre>
    <h3>三元运算符</h3>
    <pre>
      1 + 1 == 2 ? alert("成立") : alert("不成立");
      ------------
      //其实就等价于
      if(1+1=2){
        alert("成立")
      }else{
        alert("不成立")
      }
      <button @click="sanyuan">点我试试</button>
      </pre>
    <h3>switch语句</h3>
    <pre>
        //有点类似于选择题ABCD
        var a = 3;
        switch (a) {
        case 1:
          console.log("这是1");
          break;
        case 2:
          console.log("这是2");
          break;
        case 3:
          console.log("这是3");
          break;
        default:
          console.log("啥也不是");
      }
      <button @click="switchdemo">点我试试</button>
      </pre>
    <h3>循环结构</h3>
    <pre>
       //var定义一个变量a=0,当满足a<10这个条件时,执行下面的语句并a++(a = a+1)
      for (var a = 0; a < 10; a++) {
        console.log("我要打印十次");
      }
      <button @click="fordemo">点我试试</button>
      </pre>
    <h3>while循环</h3>
    <pre>
        var a = 0;  //初始变量
        while (a < 10) {  //满足这个条件就循环
        console.log(a);
        a++;  //循环记得吧a++
        }
        <button @click="whiledemo">点我试试</button>
      </pre>
    <h3>do...while</h3>
    <pre> 
        var a = 0;  //初始变量
         do {      //循环体内容
        console.log(a); //先执行一次，后判断
        a++;
        } while (a > 10); //循环判断语句
        <button @click="dowhile">点我试试</button>
      </pre>
    <h3>continue和break</h3>
    <pre>
         for (var a = 0; a < 10; a++) {
        if (a == 3) {
          continue; //到3就跳出本次循环
        }
        if (a == 6) {
          break; //到6就停止循环
        }
        console.log(a); 
        }
        //continue跳出本次循环
        //break终止循环语句
        <button @click="scontbreak">点我试试</button>
      </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    if1() {
      var a = true;
      if (a) {
        alert("成立");
      } else {
        alert("不成立");
      }
    },
    if2() {
      var a = true;
      var b = true;
      if (a) {
        alert("a成立");
      } else if (b) {
        alert("b成立");
      } else {
        alert("都不成立");
      }
    },
    sanyuan() {
      1 + 1 == 2 ? alert("成立") : alert("不成立");
    },
    switchdemo() {
      var a = 3;
      switch (a) {
        case 1:
          console.log("这是1");
          break;
        case 2:
          console.log("这是2");
          break;
        case 3:
          console.log("这是3");
          break;
        default:
          console.log("啥也不是");
      }
    },
    fordemo() {
      //var定义一个变量a=0,当满足a<10这个条件时,执行下面的语句并a++(a = a+1)
      for (var a = 0; a < 10; a++) {
        console.log("我要打印十次");
      }
    },
    whiledemo() {
      var a = 0;
      while (a < 10) {
        console.log(a);
        a++;
      }
    },
    dowhile() {
      var a = 0; //初始变量
      do {
        //循环体内容
        console.log(a); //先执行一次，后判断
        a++;
      } while (a > 10); //循环判断语句
    },
    scontbreak() {
      for (var a = 0; a < 10; a++) {
        if (a == 3) {
          continue; //到3就跳出本次循环
        }
        if (a == 6) {
          break; //到6就停止循环
        }
        console.log(a);
      }
    },
  },
};
</script>

<style lang="less" scoped></style>
