import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home'
import Welcome from '../components/Welcome'
import Init from '../components/Init'
import Variate from '../components/Variate'
import Ufunc from '../components/Ufunc'
import Control from '../components/Control'
import Arr from '../components/Arr'
import Fun from '../components/Fun'
import Obj from '../components/Obj'
import Objxg from '../components/Objxg'







Vue.use(VueRouter)

const routes = [
  {
    path: '',
    redirect: '/home'

  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: "", redirect: '/welcome' },
      { path: "/welcome", component: Welcome },
      { path: "/init", component: Init },
      { path: "/variate", component: Variate },
      { path: "/ufunc", component: Ufunc },
      { path: "/control", component: Control },
      { path: "/arr", component: Arr },
      { path: "/fun", component: Fun },
      { path: "/obj", component: Obj },
      { path: "/objxg", component: Objxg },
    ]
  }
]

const router = new VueRouter({
  /* mode: 'history', */
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
