<template>
  <div>
    <h3>函数</h3>
    <pre>
      //声明函数
      function hanshu() {
        console.log(1);
      }
      //调用函数
      hanshu();
     <button @click="fun">点我试试</button>
   </pre>
    <h3>传递参数</h3>
    <pre>
      //声明函数
      function hanshu(a, b) {
        //接收参数
        console.log(a + b);
      }
      //调用函数
      hanshu(1, 2, 3); //传递参数
      <button @click="chuandi">点我试试</button>
   </pre>
    <h3>函数返回值</h3>
    <pre>
       function fun(a, b) {
        return a + b;  //给调用该函数的人返回一个结果
      }
      var num = fun(3, 4); //var num接收返回的结果
      console.log(num);
      <button @click="fanhui">点我试试</button>
    </pre>
    <h3>arguments的使用</h3>
    <pre>
      function fun() {
        //arguments是伪数组,里面存储的是所有实参的数组
        console.log(arguments);
      }
      fun(3, 4, 5, 6);
      <button @click="argument">点我试试</button>
    </pre>
    <h3>函数其他知识</h3>
    <pre>
     // 1.函数可以调用函数
     // 2.return返回一个值会同时终止函数
     // 3.匿名函数
        var a =  function(){
        console.log('我是匿名函数');
      }
      //调用
      a();
     //4.作用域
     // 全局作用域 script 中声明的变量都是全局,都可以拿到
     // 函数内声明的变量只有函数内可读,是局部作用域
     //函数内部没有声明直接赋值也属于全局作用域

     //现阶段if{}和for{}内定义的变量是可以在外面拿到的
    </pre>
    <h3>补充知识</h3>
    <pre>
      JavaScript预解析
      变量声明和函数声明会提升到解析最前面 但是不赋值不调用
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    fun() {
      //声明函数
      function hanshu(a) {
        console.log(a);
      }
      //调用函数
      hanshu(1);
    },
    chuandi() {
      //声明函数
      function hanshu(a, b) {
        //接收参数
        console.log(a + b);
      }
      //调用函数
      hanshu(1, 2, 3); //传递参数
    },
    fanhui() {
      function fun(a, b) {
        return a + b;
      }
      var num = fun(3, 4);
      console.log(num);
    },
    argument() {
      function fun() {
        //arguments是伪数组,里面存储的是所有实参的数组
        console.log(arguments);
      }
      fun(3, 4, 5, 6);
    },
  },
};
</script>

