<template>
  <div>
    <h3>数组</h3>
    <pre>
      //数组的创建方法
      var a = new Array(); //不常用
      var b = ['张三'，李四];  //创建自带属性的数组
      var c = []; //创建空数组

      //数组的序号是从0开始数的，比如b[0]是张三，b[1]是李四
      //数组的元素是看总量的，比如说，数组b总共有2个元素
    </pre>
    <h3>遍历(循环)数组</h3>
    <pre>
      var a = ["张三", "李四", "王五"];
      for (var i = 0; i < a.length; i++) {
        //i是初始变量  length是长度的意思
        console.log(a[i]);
        //打印第a的i个，就全部循环出来了
      }
      <button @click="bianli">点我试试</button>
    </pre>
    <h3>数组的操作</h3>
    <pre>
      var a = ["张三", "李四", "王五"];
      a[0] = "陈三"; //修改第0个元素为陈三
      a[3] = "马六"; //添加第3个元素，数组现在就是4个元素了
      console.log(--a.length); //长度-1就少了个元素
    </pre>
    <h3>翻转数组</h3>
    <pre>
      var arr = ["red", "pink", "gree"];
      var newArr = [];
      for (var i = arr.length - 1; i >= 0; i--) {
        newArr[newArr.length] = arr[i];
        //循环两次  新数组的第长度个=旧数组的最后个(起)
      }
      console.log(newArr);
      <button @click="fanzhuan">点我试试</button>
    </pre>
    <h3>冒泡排序</h3>
    <pre>
      var a = [3, 5, 6, 9, 1];
      for (var i = 0; i <= a.length - 1; i++) {
        //外层管趟数
        for (var j = 0; j <= a.length - i - 1; j++) {
          //内层管交换次数
          if (a[j] > a[j + 1]) {
            //判断前一个是否大于后一个
            var tem = a[j];
            a[j] = a[j + 1];
            a[j + 1] = tem;
            //交换变量
          }
        }
      }
      console.log(a);
      <button @click="maopao">点我试试</button>
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    bianli() {
      var a = ["张三", "李四", "王五"];
      for (var i = 0; i < a.length; i++) {
        //i是初始变量  length是长度的意思
        console.log(a[i]);
        //打印第a的i个，就全部循环出来了
      }
    },
    fanzhuan() {
      var arr = ["red", "pink", "gree"];
      var newArr = [];
      for (var i = arr.length - 1; i >= 0; i--) {
        newArr[newArr.length] = arr[i];
        //循环两次  新数组的第长度个=旧数组的最后个(起)
      }
      console.log(newArr);
    },
    maopao() {
      var a = [3, 5, 6, 9, 1];
      for (var i = 0; i <= a.length - 1; i++) {
        //外层管趟数
        for (var j = 0; j <= a.length - i - 1; j++) {
          //内层管交换次数
          if (a[j] > a[j + 1]) {
            //判断前一个是否大于后一个
            var tem = a[j];
            a[j] = a[j + 1];
            a[j + 1] = tem;
            //交换变量
          }
        }
      }
      console.log(a);
    },
  },
};
</script>

