<template>
  <div>
    <h3>变量</h3>
    <button @click="bianliang">点我试试</button>
    <pre>
      var a;  //定义变量a
      console.log(a); //只定义不赋值undefined
      a = 1;      //赋值给变量
      console.log(a);
      var b = 2;  //定义并赋值变量b
      console.log(b);
      var c = 3,  
      d = 4;      //同时定义并赋值变量c和d
      console.log(c, d);
      a = 5;       //变量覆盖了
      console.log(5);
      e = 6;      //不声明直接赋值相当于给了全局变量e
      console.log(e);
      var F = 7;
      var f = 8;
      console.log(f);//结果是8，主要区分大小写
      var 18s //变量不能数字开头
      var var //变量不能是关键字</pre
    >
    <h3>小案例--交换两个变量值</h3>
    <button @click="jiaohuan">点我试试</button>
    <pre>
      var a = 1;
      var b = 2;
      var c;
      console.log("交换前a是" + a, "b是" + b);
      //开始交换
      c = a; //把a先给c存起来
      a = b; //把b给a
      b = c; //把存起来的值给b
      //交换成功
      console.log("交换后a是" + a, "b是" + b);</pre
    >
    <h3>数据的类型判断</h3>
    <pre>
         Number 数字类型-包括整形和浮点-如0,1,2.3 默认0
         Boolean 布尔型 如 true(1) false(0) 默认false(0)
         String 字符串型 如 '张三' 默认 ''
         Undefined  表示声明了变量没赋值 默认Undefined
         Null 表示空值 默认 null</pre
    >
    <h3>类型判断</h3>
    <pre>
              isNaN 判断 非数字吗？
              字符长度 var a ='哈哈哈'; 
              a.length//答案是3
              typeof a //答案是String</pre
    >
    <h3>类型转换</h3>
    <pre>
      var a = 123;  //数字型
      alert(a.toString());//转换为字符型
      alert(String(a));//强制转换为字符型
      var a = '123';  //字符型
      parseInt(a);  //转换为数字型
      parseFloat(a);  //转换为浮点类型
      Number(a);    //强制转换为数字类型
      var a = 1; //数字型
      Boolean(a)  //转换为布尔型 true
      var a = "1";
      Boolean(a)  //转换为布尔型 true</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    bianliang() {
      var a;
      console.log(a);
      a = 1;
      console.log(a);
      var b = 2;
      console.log(b);
      var c = 3,
        d = 4;
      console.log(c, d);
      a = 5;
      console.log(5);
      window.e = 6;
      console.log(window.e);
      var F = 7;
      var f = 8;
      console.log(f);
    },
    jiaohuan() {
      var a = 1;
      var b = 2;
      var c;
      console.log("交换前a是" + a, "b是" + b);
      //开始交换
      c = a; //把a先给c存起来
      a = b; //把b给a
      b = c; //把存起来的值给b
      //交换成功
      console.log("交换后a是" + a, "b是" + b);
    },
  },
};
</script>

<style lang="less" scoped></style>
