<template>
  <div>
    <h3>内置对象</h3>
    <pre>
    //内置对象是JavaScript自带的一些对象
    //比如 Math(数学相关)  Date(时间) Array(数组) String(字符串)
    //学会查阅文档 <a href="https://www.runoob.com/jsref/jsref-obj-array.html">点我查看文档</a>
  </pre>
    <h3>Math</h3>
    <pre>
      //常用函数 
      floor向下取整 ceil向上取整 round四舍五入 abs绝对值 max/min最大最小值
      random 随机数
      
      //输出最大的那个数
      console.log(Math.max(5, 10));
      //输出π
      console.log(Math.PI);

      //封装自己的数学对象
      var my = {
        min: function () {
          var a = arguments[0];
          for (var i = 0; i < arguments.length; i++) {
            if (arguments[i] < a) {
              a = arguments[i];
            }
          }
          return a;
        },
      };
      console.log(my.min(6, 8, 3, 7));
    <button @click="math">点我试试</button>
  </pre>
    <h3>Date</h3>
    <pre>
    //Date是一个构造函数需要通过new来创建
    new Date(); //当前时间
    getFullYear()//年
    getMonth()//月 +1才是真实
    getDate()//日 周日是0
    getDay()//周几
    getHours()//时
    getMinutes()//分
    getSeconds()//秒
    -----------------------------------
    getTime()//距离初始时间的毫秒数
    valueOf()//同上
    +new Date() //简单写法  常用
    Date.now() //h5新加的,更简单
    
    //倒计时算法用的是时间戳相减
    //总秒数/60/60/24天 /60/60%24小时 /60%60分 %60秒
    //new Date(时间) //输出这个时间
    //毫秒换算成秒 /1000
  </pre
    >
    <h3>格式化日期</h3>
    <pre>
         //格式化日期
         function getTime() {
        var time = new Date();
        var h = time.getHours();
        h = h < 10 ? "0" + h : h;
        var m = time.getMinutes();
        m = m < 10 ? "0" + m : m;
        var s = time.getSeconds();
        s = s < 10 ? "0" + s : s;
        return h + "时" + m + "分" + s + "秒";
      }
      console.log(getTime());
    <button @click="geshihua">点我试试</button>
  </pre>
    <h3>数组对象</h3>
    <pre>
    new Array(2)//创建一个2空元素的数组
    new Array(2,3)//创建数组[2,3]
    -------------------------------
    instanceof //检测类型
    var a = [];
    console.log(a instanceof Array)//true
    console.log(Array.isArray(a))//true
    <button @click="arrdemo">点我试试</button>
  </pre>
    <h3>增删改查排序筛选</h3>
    <pre>
    --------------增--------加----------------
    push(参数)从末尾添加一个元素----修改原数组
    unshift(参数)从头部添加一个元素----修改原数组
    --------------删--------除----------------
    pop()从尾部删除一个元素----修改原数组
    shift()从头部删除一个元素----修改原数组
    --------------排--------序----------------
    reverse()颠倒数组的元素顺序----改变原数组
    sort()对数组的元素进行冒泡排序----改变原数组
    //数字升序 直接sort是4,40,5 降序改返回b-a
    sort(function(a,b){return a-b});
    --------------查--------询----------------
     indexOf('哈',3)在数组中查找第一个该元素返回索引号或者-1   从第3个开始
     lastIndexOf()在数组中从后往前查找该元素返回索引号或者-1   从第3个开始
    --------------字---符---串----------------
    toString()数组转化为字符串 逗号分割
    join('分割符')转化为字符串 默认逗号分割,可自定义分隔符
    --------------拓--------展----------------
    concat()	//连接数组 a.concat（b）
    splice(第几个开始(index),删除几个,要添加的元素) 
    //1添加元素的位置 2需要删除后面几个元素的位置3要添加的元素
    slice(1,2) // 将数组从1开始裁剪到2结束 返回裁剪的新数组
    substring()//和上面的用法一样  但是这个不接受负值
    substr(start,length)//从start位置开始,length取的个数
    --------------其--------他---------------------------------
    for...of   copyWithin   fill   forEach  map filter  reduce
    <button @click="zsgc">点我试试</button>
  </pre>
    <h3>String</h3>
    <pre>
    //字符串修改只会开辟一个新空间
    charAt(0)//返回指定索引的元素
    charCodeAt(0)//返回指定索引的元素ASCII码
    str[0]//返回指定索引的元素  巧妙使用if可以判断
    ------------------------------------------------
    替换字符串
    replace('被替换的字符','替换为的字符');
    ------------------------------------------------
    字符分割成数组!!!   split('分隔符')
    toUpperCase转换大写
    toLowerCase转换小写
    <button @click="stringdemo">点我试试</button>
  </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    math() {
      console.log(Math.max(5, 10));
      console.log(Math.PI);
      //封装自己的数学对象
      var my = {
        min: function () {
          var a = arguments[0];
          for (var i = 0; i < arguments.length; i++) {
            if (arguments[i] < a) {
              a = arguments[i];
            }
          }
          return a;
        },
      };
      console.log(my.min(6, 8, 3, 7));
    },
    geshihua() {
      function getTime() {
        var time = new Date();
        var h = time.getHours();
        h = h < 10 ? "0" + h : h;
        var m = time.getMinutes();
        m = m < 10 ? "0" + m : m;
        var s = time.getSeconds();
        s = s < 10 ? "0" + s : s;
        return h + "时" + m + "分" + s + "秒";
      }
      console.log(getTime());
    },
    arrdemo() {
      console.log(1);
    },
    zsgc() {
      console.log(1);
    },
    stringdemo() {
      console.log(1);
    },
  },
};
</script>

<style>
a {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
}
</style>