<template>
  <div>
    <h3>创建对象</h3>
    <pre>
       var a = {}; //创建对象
      //创建对象同时赋值
      var b = {
        name: "张三",
        age: 18,
        sayHi: function () {
          console.log("hi~");
        },
      };
      console.log(b.name); //输出对象里的name
      console.log(b["name"]); //输出对象里的name
      b.sayHi(); //调用对象里的方法
      
      var obj = new Object(); //创建对象
      //给对象里追加属性
      obj.name = "李四";  
      obj.age = 19;
      obj.sayHi = function () {
        console.log("hi~");
      };
      <button @click="init">点我试试</button>
    </pre>
    <h3>构造函数</h3>
    <pre>
       //构造函数名字开头要大写
      function Star(name, age) {
        this.name = name;
        this.age = age;
      }
      var a = new Star("张三", 19);
      console.log(a);
      <button @click="gouzao">点我试试</button>
    </pre>
    <h3>for..in遍历对象</h3>
    <pre>
      //遍历数组是for循环,遍历对象是for...in
      var b = {
        name: "张三",
        age: 18,
      };
      for (var k in b) {
        //k得到属性名
        console.log(b[k]);
        //打印属性值
      }
      <button @click="forin">点我试试</button>
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    init() {
      var a = {}; //创建对象
      //创建对象同时赋值
      var b = {
        name: "张三",
        age: 18,
        sayHi: function () {
          console.log("hi~");
        },
      };
      console.log(b.name); //输出对象里的name
      console.log(b["name"]); //输出对象里的name
      b.sayHi(); //调用对象里的方法
      var obj = new Object(); //创建对象
      //给对象里追加属性
      obj.name = "李四";
      obj.age = 19;
      obj.sayHi = function () {
        console.log("hi~");
      };
    },
    gouzao() {
      //构造函数名字开头要大写
      function Star(name, age) {
        this.name = name;
        this.age = age;
      }
      var a = new Star("张三", 19);
      console.log(a);
    },
    forin() {
      var b = {
        name: "张三",
        age: 18,
      };
      for (var k in b) {
        //k得到属性名
        console.log(b[k]);
        //打印属性值
      }
    },
  },
};
</script>

