<template>
  <div>
    <h3>基础运算+递增/减运算符</h3>
    <pre>
        加+  减+  乘*  除/  取余%  括号()
        var a = 1 + 1;
        比如
        var a
        a = a + 1   //他就等价于
        a+=1        //他就等价于
        a++         //他就等价于
        或者写成  ++a 
        ------------------------------------------
        前置++和后置++的区别
        前置++先自增后运算
        后置++先计算后自增
        var a = 1;
        ++a + 10   //答案是12
        a++ + 10  //答案是11
   </pre
    >
    <h3>比较运算符+逻辑运算符+赋值运算符</h3>
    <pre>
        比较运算符
        大于>  小于<  大于等于>=  小于等于<=  等于==(用于判断)  不等于!=  全等===
        逻辑运算符
        与&&  或||  非!
        比如---    1==1&&2==2  //两边都成立true
        比如---    1==1||2==3  //有一边成立true
        比如---    1==2||2==3  //两边都不成立false
        比如---    !true  //非true就是false
        赋值运算符
        赋值=     比如 var a =1;
        a+=2  等价于a=a+2
        +=  -+  *+  /+  %= 同理</pre
    >
    <h3>运算符优先级</h3>
    <pre>
      1.括号()
      2.一元运算符  就是  ++ -- !
      3.算数运算符  先*/后+-
      4.关系运算符  > >= < <=
      5.相等运算符  == != === !==
      6.逻辑运算符  先&&后||
      7.赋值运算符  =</pre
    >
  </div>
</template>

<script>
export default {};
</script>

